import React, { useState } from 'react'
import { BsEyeSlash, BsEye } from 'react-icons/bs';
import Navbar from './Navbar';


export default function Login() {
    const [passwordType, setPasswordType] = useState("password");
    const [passwordInput, setPasswordInput] = useState("");
    const handlePasswordChange = (evnt) => {
        setPasswordInput(evnt.target.value);
    }
    const togglePassword = () => {
        if (passwordType === "password") {
            setPasswordType("text")
            return;
        }
        setPasswordType("password")
    }
    document.body.style.backgroundColor = "#ecf0f1";

    return (
        <>
        <Navbar value={"Sign up"}/>
            <div className="flex min-h-full flex-1 flex-col justify-center px-6 py-1 lg:px-8 mb-20">
                <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm border-grey-500 p-8 shadow-2xl rounded-lg bg-white">
                    <form className="space-y-6">
                        <div>
                            <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                                Email address
                            </label>
                            <div className="mt-2">
                                <input
                                    id="email"
                                    name="email"
                                    type="email"
                                    autoComplete="email"

                                    className=" pl-3 block w-full rounded-md border-2 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inse sm:text-sm sm:leading-6"
                                />
                            </div>
                        </div>

                        <div>
                            <div className="flex items-center justify-between">
                                <label htmlFor="password" className="block text-sm font-medium leading-6 text-gray-900">
                                    Password
                                </label>
                            </div>
                            <div className="mt-2 flex relative">
                                <input
                                    id="password"
                                    name="password"
                                    type={passwordType}
                                    onChange={handlePasswordChange}
                                    value={passwordInput}
                                    className=" pl-3 block w-full rounded-md border-2 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset sm:text-sm sm:leading-6"
                                />
                                <span className="absolute right-2 top-2  cursor-pointer " onClick={togglePassword}>
                                    {passwordType === "password" ? <BsEyeSlash size={20}/> : <BsEye size={20}/>}
                                </span>
                            </div>
                            <div className="text-sm text-center pt-5">
                                <a href="#" className="font-semibold text-blue-500 hover:text-blue-600">
                                    Forgotten password?
                                </a>
                            </div>
                        </div>

                        <div>
                            <button
                                type="submit"
                                className="flex w-full justify-center rounded-md bg-blue-500 px-3 py-1.5 text-sm font-semibold leading-6 text-white shadow-sm hover:bg-blue-600 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-blue-600"
                            >
                                Sign in
                            </button>
                        </div>
                    </form>



                </div>
            </div >


        </>
    )
}
