import React, { useState } from "react";
import { Transition } from "@headlessui/react";
import { VscThreeBars } from 'react-icons/vsc';
import { GrClose } from 'react-icons/gr';

function Navbar(props) {
    const navList = ["Home", "Products", "Categroies", "Contact", "About"]
    const [isOpen, setIsOpen] = useState(false);
    return (
        <div>
            <nav className="bg-gray-800 ">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex items-center justify-between h-16">
                        <div className="flex items-center">
                            <div className="flex-shrink-0">
                                <h1 className="text-white text-2xl font-bold uppercase">Navbar</h1>
                            </div>
                        </div>
                        <div className="hidden md:block ml-10">
                            <div className="flex items-center space-x-4 ">
                                {
                                    navList.map((list) => {
                                        return (
                                            <>
                                                <a href="#" className="hover:bg-gray-700 text-white block px-3 py-2 rounded-md text-base font-medium">{list}</a>
                                            </>
                                        )
                                    })
                                }
                            </div>
                        </div>
                        <div className="flex items-center gap-3">
                            <button className="rounded-lg font-medium text-white hover:bg-blue-600 bg-blue-500 py-1.5 px-3">{props.value}</button>

                        </div>
                        <div className="-mr-2 flex md:hidden">
                            <button
                                onClick={() => setIsOpen(!isOpen)}
                                type="button"
                                className="inline-flex items-center justify-center p-2 rounded-md text-white hover:text-white"
                                aria-controls="mobile-menu"
                                aria-expanded="false"
                            >
                                <span className="sr-only">Open main menu</span>
                                {!isOpen ? (
                                    <VscThreeBars size={26} />
                                ) : (
                                    <GrClose size={26} color="white" className="hover:text-white" />
                                )}
                            </button>
                        </div>
                    </div>
                </div>

                <Transition
                    show={isOpen}
                    enter="transition ease-out duration-100 transform"
                    enterFrom="opacity-0 scale-95"
                    enterTo="opacity-100 scale-100"
                    leave="transition ease-in duration-75 transform"
                    leaveFrom="opacity-100 scale-100"
                    leaveTo="opacity-0 scale-95"
                >
                    {(ref) => (
                        <div className="md:hidden" id="mobile-menu">
                            <div ref={ref} className="px-2 pt-2 pb-3 space-y-1 sm:px-3">
                                {
                                    navList.map((list) => {
                                        return (
                                            <>
                                                <a href="#" className="hover:bg-gray-700 text-white block px-3 py-2 rounded-md text-base font-medium">{list}</a>
                                            </>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    )}
                </Transition>
            </nav>


        </div>
    );
}

export default Navbar;